.App {
  width: 640px;
  $cal-font-size: 48px;
  text-align: center;
  display: flex;
  flex-direction: column;
  color: cornflowerblue;
  #award_target {
    margin: 20px 20px 2px;
  }
  #item_count {
    margin: 0px 20px 0px;
    font-size: $cal-font-size;
  }
  .cal-item {
    font-size: $cal-font-size;
    display: flex;
    flex-direction: row;
    margin-top: 10px;
    .op-item {
      width: calc($cal-font-size * 2);
    }
    .op {
      width: $cal-font-size;
      color: darkblue;
    }
    .op-ret {
      font-size: $cal-font-size;
      width: calc($cal-font-size * 2);
      color: rgb(175, 5, 197);
    }
    .op-right {
      color: green;
    }
    .op-wrong {
      color: red;
    }
    .op-right-ret {
      margin-left: $cal-font-size;
      font-size: calc($cal-font-size/2);
      color: green;
    }
  }
}
